.scan-process__title {
  margin-bottom: 16px;

  font-family: 'Cera Pro';
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.28;

  text-transform: uppercase;

  color: $secondary-color-text;

  @include tablet {
    font-size: 24px;
    line-height: 1.25;
    margin-bottom: 24px;
    padding-left: 28px;
  }

  @include desktop {
    font-size: 36px;
    margin-bottom: 40px;
    padding-left: 117px;
  }
}

.scan-process__list {
  display: flex;
  flex-wrap: wrap;
  row-gap: 16px;
  column-gap: 20px;

  @include tablet {
    column-gap: 32px;
  }
}

.scan-process__item {
  @include centered-flex;
  gap: 15px;
  height: 182px;
  flex-basis: calc((100% - 1 * 20px) / 2);
  box-shadow: inset 1px 1px 16px rgba(203, 203, 203, 0.8),
    inset -1px -1px 16px #ffffff;
  border-radius: 50px 0px;
  flex-direction: column;
  text-align: center;

  @include tablet {
    height: 210px;
    flex-basis: calc((100% - 3 * 32px) / 4);
  }
  @include desktop {
    height: 381px;
  }
}

.scan-process__item:nth-child(1)::before {
  content: '';
  background-image: url('../images/mobile/3d-scanning-page/scan-process-1-mobile@1x.jpg');
  width: 91.76px;
  height: 81.44px;
  display: block;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;

  @include retina {
    background-image: url('../images/mobile/3d-scanning-page/scan-process-1-mobile@2x.jpg');
  }

  @include retina3x {
    background-image: url('../images/mobile/3d-scanning-page/scan-process-1-mobile@3x.jpg');
  }

  @include tablet {
    width: 107.29px;
    height: 93.97px;
    background-image: url('../images/tablet/3d-scanning-page/scan-process-1-tablet@1x.jpg');
    @include retina {
      background-image: url('../images/tablet/3d-scanning-page/scan-process-1-tablet@2x.jpg');
    }

    @include retina3x {
      background-image: url('../images/tablet/3d-scanning-page/scan-process-1-tablet@3x.jpg');
    }
  }

  @include desktop {
    width: 240px;
    height: 240px;
    background-image: url('../images/desktop/3d-scanning-page/scan-process-1-desktop@1x.jpg');
    @include retina {
      background-image: url('../images/desktop/3d-scanning-page/scan-process-1-desktop@2x.jpg');
    }

    @include retina3x {
      background-image: url('../images/desktop/3d-scanning-page/scan-process-1-desktop@3x.jpg');
    }
  }
}
